.App {
  text-align: center;
}

.App h1 {
  color: #ff2052;
  text-shadow: 1px 1px #333;
  margin: 0;
}

.App ul {
  list-style: none;
  margin: 0;
  padding: 0 1em;
  text-align: left;
}

.powered-by {
  font-size: small;
  color: #ff2052;
  text-decoration: none;
  font-weight: bold;
}

.loading {
  padding: 1em;
}

.hide {
  display: none;
}

.nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0 2em;
}

select {
  font-size: 1em;
  border: 1px solid #666;
  border-radius: 0px;
  margin: 0.5em 0;
}

.main a {
  text-decoration: none;
  color: #333;
  text-shadow: 1px 1px #ff2052;
}

.main .author {
  font-size: 1em;
  font-style: italic;
  color: #666;
}



